@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
}

body {
  max-width: 1920px;
  margin: 0 auto;
}

.home__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 6%;
}

.home__header a {
  color: inherit;
  text-decoration: none;
}

.home__header h1 {
  font-size: 36px;
  font-size: 600;
  cursor: pointer;
}

.home__header ul {
  list-style-type: none;
  display: flex;
  align-items: center;
}

.home__header ul li {
  font-size: 16px;
  font-weight: 600;
  margin-right: 30px;
  cursor: pointer;
  position: relative;
}

.home__header ul li span {
  width: 100%;
  height: 1px;
  background-color: black;
  position: absolute;
  bottom: -1px;
  left: 0;
  transform: scaleX(0);
  transform-origin: 100% 50%;
  transition: transform 250ms ease-in-out;
}

.home__header ul li:hover span,
.home__header ul li:focus span {
  transform: scaleX(1);
  transform-origin: 0 50%;
}

.home__header ul li:last-child {
  margin-right: 0;
}

.home__header__hamburger {
  display: none;
  cursor: pointer;
  width: 24px;
}

.home__header__hamburger__line {
  width: 100%;
  height: 2px;
  background-color: #000000;
  margin-bottom: 4px;
  border-radius: 4px;
  transition: width 0.3s ease;
}

.home__header__hamburger__line-2 {
  width: 20px;
}

.home__header__hamburger__line-3 {
  width: 16px;
}

.home__header__hamburger__line:last-child {
  margin-bottom: 0;
}

.home__header__hamburger:hover .home__header__hamburger__line {
  width: 100%;
}

.home__landing {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: calc(100vh - 89px - 50px);
  margin-top: 50px;
  padding: 0 6%;
  overflow: hidden;
}

.home__landing__content {
  width: 60%;
  margin-right: 30px;
  border-left: 5px solid black;
  padding: 10px 0;
  padding-left: 35px;
  visibility: hidden;
}

.home__landing__content h1 {
  margin-bottom: 35px;
  width: 90%;
  font-weight: 700;
  text-transform: capitalize;
}

.home__landing__content p {
  margin-bottom: 40px;
  width: 70%;
}

.home__landing__content a {
  text-decoration: none;
  color: inherit;
}

.home__landing__content button {
  background-color: black;
  border: none;
  color: white;
  padding: 15px 35px;
  cursor: pointer;
  border-radius: 4px;
  margin-right: 30px;
  font-weight: 600;
  font-size: 16px;
  position: relative;
}

.home__landing__content button span {
  width: 100%;
  height: 1px;
  background-color: white;
  display: block;
  transform: scaleX(0);
  transform-origin: 100% 50%;
  transition: transform 250ms ease-in-out;
}

.home__landing__content button:hover span {
  transform: scaleX(1);
  transform-origin: 0 50%;
}

.home__landing__img {
  width: calc(39.21% - 35px);
  background-color: #e9e9e9;
  padding-top: calc((39.31% - 35px) * 1.2446);
  position: relative;
  visibility: hidden;
}

.home__landing__img img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.home__gallery {
  margin: 50px 6% 0 !important;
  position: relative;
}

.home__gallery__pagination {
  cursor: pointer;
  z-index: 10;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: absolute;
  top: 50%;
}

.home__gallery__pagination-prev {
  left: 0;
}

.home__gallery__pagination-prev span {
  margin-left: 10px;
  font-weight: 600;
}

.home__gallery__pagination-next {
  right: 0;
}

.home__gallery__pagination-next span {
  margin-right: 10px;
  font-weight: 600;
}

.home__gallery__container {
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
}

.home__gallery__container__img-container {
  width: 62.8%;
  padding-top: calc(62.8% * 0.6882);
  position: relative;
  background-color: #e9e9e9;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.home__gallery__container__img-container img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.home__gallery__container__desc {
  max-width: 210px;
  min-height: 270px;
  padding: 25px 20px 60px;
  box-shadow: 4px 4px 10px 2px rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  background-color: white;
}

.home__gallery__container__desc h3 {
  font-size: 20px;
  font-weight: 600;
  border-bottom: 2px solid black;
  margin-bottom: 30px;
}

.home__gallery__container__desc p {
  font-size: 14px;
}

.home__about {
  margin-top: 90px;
  visibility: hidden;
}

.home__about__line {
  width: 40%;
  height: 2px;
  background-color: black;
}

.home__about__line2 {
  float: right;
}

.home__about__container {
  margin: 0 auto;
  margin-top: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 6%;
  max-width: 1442px;
}

.home__about__container p {
  font-size: 16px;
}

.home__about__container p span {
  font-weight: 600;
  font-size: 20px;
}

.home__about__container__img-container {
  min-width: 257px;
  max-width: 257px;
  margin-left: 50px;
  padding-top: 263px;
  background-color: #e9e9e9;
  position: relative;
}

.home__about__container__img-container img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.home__about__container1 {
  margin-bottom: 60px;
}

.home__about__container1 .home__about__container__img-container {
  min-width: 400px;
  max-width: 400px;
  margin-left: 0;
  margin-right: 50px;
}

.home__footer {
  margin-top: 100px;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
}

.home__footer p {
  color: #646363;
  font-size: 14px;
  text-align: center;
}

@media only screen and (max-width: 1400px) {
  .home__landing__content {
    width: 100%;
  }
  .home__landing__img {
    display: none;
  }
  .home__gallery {
    margin-left: 3% !important;
    margin-right: 3% !important;
  }
  .home__gallery__pagination {
    top: 45%;
  }
  .home__gallery__container {
    flex-direction: column;
  }
  .home__gallery__container__img-container {
    width: 75%;
    padding-top: calc(75% * 0.6882);
  }
  .home__gallery__container__desc {
    width: 75%;
    transform: none;
    max-width: none;
    height: auto;
    min-height: auto;
    box-shadow: none;
    padding: 0%;
    margin-top: 20px;
  }
  .home__gallery__container__desc h3 {
    margin-bottom: 20px;
  }
  .home__gallery__container__desc p {
    font-size: 14px;
  }
}

@media only screen and (max-width: 1000px) {
  .home__about__line {
    display: none;
  }
  .home__about__container {
    flex-direction: column;
  }
  .home__about__container p {
    margin-top: 30px;
  }
  .home__about__container__img-container {
    margin: 0 !important;
  }
  .home__about__container0 p {
    order: 1 !important;
  }
}

@media only screen and (max-width: 800px) {
  .home__landing {
    padding: 0 3%;
  }
  .home__landing__content {
    border: none;
    text-align: center;
    margin-right: 0;
    padding: 0;
  }
  .home__landing__content h1 {
    width: 100%;
  }
  .home__landing__content p {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
  }
  .home__landing__content button {
    margin-right: 0;
  }
  .home__landing__img {
    display: none;
  }
  .home__gallery__pagination span {
    display: none;
  }
  .home__gallery__container__img-container {
    width: 80%;
    padding-top: calc(80% * 0.6882);
  }
  .home__gallery__container__desc {
    width: 80%;
  }
}

@media only screen and (max-width: 500px) {
  .home__header h1 {
    font-size: 27px;
  }
  .home__header__nav-links {
    background-color: #c5c5c5;
    width: 100%;
    position: absolute;
    z-index: 99999999;
    top: 70px;
    right: 0;
    padding: 20px;
    flex-direction: column;
    transform: scaleY(0);
    transform-origin: top;
    transition-duration: 0.3s;
    transition-property: transform;
    transition-timing-function: ease-out;
    transition-delay: 0.1s;
  }
  .home__header__nav-links--visible {
    transform: scaleY(1);
    transition-delay: 0s !important;
  }
  .home__header__nav-links--visible li {
    opacity: 1 !important;
    transform: translateY(0) !important;
    transition-delay: 0.2s !important;
  }
  .home__header__nav-links li {
    margin: 0 !important;
    margin-bottom: 13px !important;
    font-weight: 400;
    opacity: 0;
    text-align: center;
    transform: translateY(-10px);
    transition-property: transform, opacity;
    transition-duration: 0.3s;
    transition-delay: 0s;
  }
  .home__header__nav-links li a {
    text-align: center;
    width: 100%;
  }
  .home__header__hamburger {
    display: inline;
    display: initial;
  }
  .home__landing {
    padding: 0 3%;
  }
  .home__landing__content {
    border: none;
    text-align: center;
    margin-right: 0;
    padding: 0;
  }
  .home__landing__content h1 {
    width: 100%;
    font-size: 25px;
  }
  .home__landing__content p {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    font-size: 14px;
  }
  .home__landing__content button {
    margin-right: 0;
  }
  .home__landing__img {
    display: none;
  }
  .home__gallery__pagination {
    top: 30%;
  }
  .home__about__container1 .home__about__container__img-container {
    min-width: auto;
    width: 90%;
    padding-top: calc(90% * 0.6572);
  }
}
